import { getClient } from './WebdavClient';
import { genFileInfo } from '../utils/fileUtils';
import { createClient } from 'webdav';
import { getRequestToken } from '@nextcloud/auth';
const statData = "<?xml version=\"1.0\"?>\n<d:propfind  xmlns:d=\"DAV:\"\n\txmlns:oc=\"http://owncloud.org/ns\"\n\txmlns:nc=\"http://nextcloud.org/ns\"\n\txmlns:ocs=\"http://open-collaboration-services.org/ns\">\n\t<d:prop>\n\t\t<d:getlastmodified />\n\t\t<d:getcontenttype />\n\t\t<d:resourcetype />\n\t\t<d:getetag />\n\t\t<oc:fileid />\n\t\t<oc:permissions />\n\t\t<oc:size />\n\t\t<d:getcontentlength />\n\t\t<nc:has-preview />\n\t\t<nc:mount-type />\n\t\t<nc:is-encrypted />\n\t\t<ocs:share-permissions />\n\t\t<oc:tags />\n\t\t<oc:favorite />\n\t\t<oc:comments-unread />\n\t\t<oc:owner-id />\n\t\t<oc:owner-display-name />\n\t\t<oc:share-types />\n\t</d:prop>\n</d:propfind>";
/**
 * Retrieve the files list
 * @param path
 * @param options
 */
export default async function (path) {
  let options = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : {};
  const response = await getClient().stat(path, Object.assign({
    data: statData,
    details: true
  }, options));
  return genFileInfo(response.data);
}
/**
 * Retrieve the files list
 * @param origin
 * @param path
 * @param options
 */
export async function rawStat(origin, path) {
  let options = arguments.length > 2 && arguments[2] !== undefined ? arguments[2] : {};
  const response = await createClient(origin, {
    headers: {
      requesttoken: getRequestToken() || ''
    }
  }).stat(path, {
    ...options,
    data: statData,
    details: true
  });
  return response.data;
}